<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
.jobconfig .el-table th{
  text-align: center
}


</style>
<template>
  <div class="jobconfig">
   <el-container>
  <el-header style="background-image: url('./static/img/yunjian22.png'); background-size: cover; background-position: top center;">流数据清洗工具</el-header>
  <el-container>
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)"> <nevigation></nevigation></el-aside>
    <el-main>
      <h3 style="text-align:right">
              <i class="el-icon-info">
              </i>
              任务配置
            </h3>
      <newjob @fresh="getJob()"></newjob>
   <el-row :gutter="10">
    <el-col :span="6"><div class="grid-content" style="text-align:left">
          <i class="el-icon-date" >
        </i> <a>任务列表</a>
    </div></el-col>
  <el-col :span="18"><div class="grid-content" ></div></el-col>
  <el-row>
    <el-col :span="24">
        <el-table
    :data="tableData4"
    style="width: 100%"
    max-height="300">
    <el-table-column
      fixed
      label="任务名称"
      width="150">
    <template slot-scope="scope">
      <span v-show="!scope.row.modcheck">{{scope.row.jobname}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="jobname" placeholder="请输入内容"></el-input>
      </span>
    </template>
    </el-table-column>
    <el-table-column
      label="任务描述"
      width="200">
     <template slot-scope="scope">
      <span v-show="!scope.row.modcheck">{{scope.row.jobdesc}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="jobdesc" placeholder="请输入内容"></el-input>
      </span>
    </template>
    </el-table-column>
    <el-table-column
      label="数据源类型"
      width="200">
    <template slot-scope="scope">
      <span v-show="!scope.row.modcheck">{{scope.row.datatype}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="datatype" placeholder="请输入内容"></el-input>
      </span>
    </template>
    </el-table-column>
    <el-table-column
      label="数据源名称"
      width="200">
    <template slot-scope="scope">
      <span v-show="!scope.row.modcheck">{{scope.row.dataname}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="dataname" placeholder="请输入内容"></el-input>
      </span>
    </template>
    </el-table-column>
    <el-table-column
      label="算法类型"
      width="120">
   <template slot-scope="scope">
      <span v-show="!scope.row.modcheck">{{scope.row.algtype}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="algtype" placeholder="请输入内容"></el-input>
      </span>
    </template>
    </el-table-column>
    <el-table-column
      label="算法名称"
      width="120">
   <template slot-scope="scope">
      <span v-show="!scope.row.modcheck">{{scope.row.algname}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="algname" placeholder="请输入内容"></el-input>
      </span>
    </template>
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      width="200">
      <template slot-scope="scope">
     <el-button
          @click.native.prevent="deleteconfirm(scope.$index, scope.row,tableData4)"
          type="text"
          size="small"
          plain
          v-show="!scope.row.modcheck">
          移除
        </el-button>
        <el-button
          @click.native.prevent="modcheckit(scope.$index, scope.row,tableData4)"
          type="text"
          size="small"
          v-show="!scope.row.modcheck"
          @click="dialogFormVisible = true">
          修改
        </el-button>
           <el-button
          @click.native.prevent="modchecksuc(scope.$index, scope.row,tableData4)"
          type="text"
          size="small"
          plain
          v-show="scope.row.modcheck">
          确定
        </el-button>
        <el-button
          @click.native.prevent="unmodcheck(scope.$index,scope.row, tableData4)"
          type="text"
          size="small"
          v-show="scope.row.modcheck">
          返回
        </el-button> 
      </template>
    </el-table-column>
  </el-table>
    </el-col>
  </el-row>
</el-row>
<el-dialog title="修改任务" :visible.sync="dialogFormVisible">
  <el-form :model="form">
    <el-row>
      <el-col :span="12">
    <el-form-item label="任务名称" :label-width="formLabelWidth">
      <el-input v-model="form.jobname" ></el-input>
    </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item label="任务描述" :label-width="formLabelWidth">
      <el-input v-model="form.jobdesc" ></el-input>
    </el-form-item>
      </el-col>
    </el-row>
     <el-row>
      <el-col :span="12">
    <el-form-item label="数据源类型" :label-width="formLabelWidth">
      <el-input v-model="form.datatype" disabled></el-input>
    </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item label="数据源名称" :label-width="formLabelWidth">
      <el-input v-model="form.dataname" disabled></el-input>
    </el-form-item>
      </el-col>
    </el-row>
    <el-row>
    <el-col :span="24">
    <el-table
    :data="Datasoucetable"
    style="width: 100%"
    max-height="200">
    <el-table-column
      fixed
      prop="type"
      label="数据源类型"
      width="150">
    </el-table-column>
    <el-table-column
      prop="name"
      label="数据源名称"
      width="200">
    </el-table-column>
    <el-table-column
      prop="desc"
      label="数据源描述"
      width="200">
    </el-table-column>
    <el-table-column
      prop="tablename"
      label="表名"
      width="200">
    </el-table-column>
    <el-table-column
      prop="comma"
      label="分隔符"
      width="120">
    </el-table-column>
    <el-table-column
      prop="first"
      label="第一列列名"
      width="120">
    </el-table-column>
       <el-table-column
      prop="database"
      label="数据库名"
      width="200">
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      width="200">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="selectDatesource(scope.$index,scope.row,Datasoucetable)"
          type="text"
          size="small">
          选择
        </el-button>
      </template>
    </el-table-column>
  </el-table>
   </el-col>
    </el-row>
         <el-row>
      <el-col :span="12">
    <el-form-item label="算法类型" :label-width="formLabelWidth">
      <el-input v-model="form.algtype" disabled></el-input>
    </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item label="算法名称" :label-width="formLabelWidth">
      <el-input v-model="form.algname" disabled></el-input>
    </el-form-item>
      </el-col>
    </el-row>
    <el-row>
       <el-col :span="24">
 <el-table
    :data="Algtable"
    style="width: 100%"
    max-height="200">
    <el-table-column
      fixed
      prop="algid"
      label="算法ID"
      width="150">
    </el-table-column>
    <el-table-column
      prop="algtype"
      label="算法类型"
      width="200">
    </el-table-column>
    <el-table-column
      prop="algname"
      label="算法名称"
      width="200">
    </el-table-column>
    <el-table-column
      prop="jarname"
      label="算法包"
      width="200">
    </el-table-column>
    <el-table-column
      prop="algdesc"
      label="算法描述"
      width="120">
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      width="200">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="selectalg(scope.$index,scope.row, Algtable)"
          type="text"
          size="small">
          选择
        </el-button>
      </template>
    </el-table-column>
  </el-table>
       </el-col>
     </el-row>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false">取 消</el-button>
    <el-button type="primary" @click="modchecksuc()">更 新</el-button>
  </div>
</el-dialog>
    </el-main>
  </el-container>
</el-container>
  </div>
</template>
<script>
  import nevigation from "./sub/nevigation.vue";
  import newjob from "./sub/newjob.vue";
  export default {
    components: {
      nevigation,
      newjob,
    },
    data() {
      return {
         tableData4: [],
          dialogFormVisible: false,
        form: {
          jobid:'',
          jobname: '',
          jobdesc: '',
          datatype: '',
          datename: '',
          datafile:'',
          algname: '',
          algtype: '',
          algfile:'',
        },
        formLabelWidth: '120px'
      }
    },
    created: function () {
      var vm = this;
      vm.getJob();
      vm.getDatasouce();
      vm.getAlg();

    },

    methods: {
       getDatasouce(){
        var vm = this;
        vm.Datasoucetable = [];
         var url = "http://localhost:8081/datasource";
        var data_tmp = {
        };
       $.ajax({
          url: url,
          type: "GET",
          dataType: 'json',
          data: {
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data.data;
            if(data.code==0){
            if(Obj.length>0){
              for (var i = 0; i < Obj.length; i++) {
                vm.Datasoucetable.push({
                  "type":Obj[i].datatype,
                  "name":Obj[i].dataname,
                  "desc":Obj[i].datadesc,
                  "tablename":Obj[i].tablename,
                  "comma":Obj[i].coma,
                  "first":Obj[i].firstline,
                  "database":Obj[i].databasename
                });
                
              }}}
              else{
                console.log(data);
              }
          },
           error: function (data) {
            console.log(data);
          }
        });
      },
      getAlg(){
        var vm = this;
        vm.Algtable = [];
         var url = "http://localhost:8081/alg";
        var data_tmp = {
        };
       $.ajax({
          url: url,
          type: "GET",
          dataType: 'json',
          data: {
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data.data;
            if(data.code==0){
            if(Obj.length>0){
              for (var i = 0; i < Obj.length; i++) {
                vm.Algtable.push({
                  "algid":Obj[i].algid,
                  "algtype":Obj[i].algtype,
                  "algname":Obj[i].algname,
                  "jarname":Obj[i].jarname,
                  "algdesc":Obj[i].algdesc,
                  
                });
                
              }}}
              else{
                console.log(data);
              }
          },
           error: function (data) {
            console.log(data);
          }
        });    
      },
      getJob(){
         var vm = this;
        vm.tableData4 = [];
         var url = "http://localhost:8081/job";
        var data_tmp = {
        };
       $.ajax({
          url: url,
          type: "GET",
          dataType: 'json',
          data: {
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data.data;
            if(data.code == 0){
            if(Obj.length>0){
              for (var i = 0; i < Obj.length; i++) {
                vm.tableData4.push({
                  "jobid":Obj[i].jobid,
                  "jobname":Obj[i].jobname,
                  "jobdesc":Obj[i].jobdesc,
                  "datatype":Obj[i].datatype,
                  "dataname":Obj[i].dataname,
                  "datafile":Obj[i].datafile,
                  "algtype":Obj[i].algtype,
                  "algname":Obj[i].algname,
                  "algfile":Obj[i].algfile,
                  "modcheck":false,
                });
                
              }}}
              else{
                console.log(data);
              }
          },
           error: function (data) {
            console.log(data);
          }
        });
      },
        deleteconfirm:function(index,row,tableData4){
            var vm=this;
            var r=confirm("请确认是否删除");
            if (r==true)
            {
              vm.deletejob(index,row,tableData4);
            }
            else
            {
              return;
            }
          },
      deletejob(index,row,tableData4){
         var vm = this;
         var url = "http://localhost:8081/job/deleteone";
       $.ajax({
          url: url,
          type: "POST",
          dataType: 'json',
          data: {
             "jobid":row.jobid,
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data;
             if(data.code==0){
             vm.$notify({
             title: '成功',
             message: "删除成功",
             type:'success'
             });}
            else{
             vm.$notify.error({
             title: '错误',
             message: data.msg,
             });
            }
            vm.getJob();
                
      
          },
           error: function (data) {
            console.log(data);
          }
        });
      },
      selectDatesource(index,row,Datasoucetable){
       var vm=this;
       vm.form.datatype=row.type;
       vm.form.dataname=row.name;
       vm.form.datafile=row.database;
      },
      selectalg(index,row,Algtable){
        var vm=this;
        vm.form.algtype=row.algtype;
        vm.form.algname=row.algname;
        vm.form.algfile=row.jarname;
      },
      modcheckit(index,row,tableData4){
        var vm=this;
        vm.dialogFormVisible=true;
        vm.form.jobid=row.jobid;
        vm.form.jobname=row.jobname;
        vm.form.jobdesc=row.jobdesc;
        vm.form.datatype=row.datatype;
        vm.form.datename=row.datename;
        vm.form.datafile=row.datafile;
        vm.form.algtype=row.algtype;
        vm.form.algname=row.algname;
        vm.form.algfile=row.algfile;
      },
      modchecksuc(){
         var vm =this;
       var url ="http://localhost:8081/job/update"
       $.ajax({
         url:url,
         type:"POST",
         dataType:"json",
         data:{
            "jobid":vm.form.jobid,
            "jobname":vm.form.jobname,
            "jobdesc":vm.form.jobdesc,
            "datatype":vm.form.datatype,
            "dataname":vm.form.datename,
            "datafile":vm.form.datafile,
            "algtype":vm.form.algtype,
            "algname":vm.form.algname,
            "algfile":vm.form.algfile,
         },
          success:function(data){
            if(data.code == 0){
            vm.$notify({
             title: '成功',
             message: '修改成功',
             type: 'success'
             });
           }
            else{
            vm.$notify.error({
             title: '错误',
             message: data.msg,
             });
            }
            console.log(data);
            vm.getAlg();
       },
       error:function(data){
          vm.$notify.error({
             title: '错误',
             message: "网络错误",
             });
            console.log(data);
       }
       }
      )
      }

    }

  }

</script>



