<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.result .el-table th{
  text-align: center
}


</style>
<template>
  <div class="result">
    <el-container>
  <el-header style="background-image: url('./static/img/yunjian22.png'); background-size: cover; background-position: top center;">流数据清洗工具</el-header>
  <el-container>
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)"> <nevigation></nevigation></el-aside>
    <el-main>
      <h3 style="text-align:right">
              <i class="el-icon-info">
              </i>
              结果查看
            </h3>
   <el-row :gutter="10">
    <el-col :span="6"><div class="grid-content" style="text-align:left">
          <i class="el-icon-date" >
        </i> <a>结果报表</a>
    </div></el-col>
  <el-col :span="18"><div class="grid-content" ></div></el-col>
  <el-row>
    <el-col :span="24">
        <el-table
    :data="tableData4"
    style="width: 100%"
    max-height="500">
    <el-table-column
      fixed
      prop="jobname"
      label="任务名称"
      width="150">
    </el-table-column>
    <el-table-column
      prop="jobdesc"
      label="任务描述"
      width="200">
    </el-table-column>
    <el-table-column
      prop="datatype"
      label="数据源类型"
      width="200">
    </el-table-column>
    <el-table-column
      prop="dataname"
      label="数据源名称"
      width="200">
    </el-table-column>
    <el-table-column
      prop="algtype"
      label="算法类型"
      width="120">
    </el-table-column>
    <el-table-column
      prop="algname"
      label="算法名称"
      width="120">
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      width="200">
      <template slot-scope="scope">
          <el-button
          @click.native.prevent="deleteRow(scope.$index, tableData4)"
          type="text"
          size="small">
          下载结果
        </el-button>
      </template>
    </el-table-column>
  </el-table>
    </el-col>
  </el-row>
</el-row>
    </el-main>
  </el-container>
</el-container>
  </div>
</template>
<script>
  import nevigation from "./sub/nevigation.vue";

  export default {
    components: {
      nevigation,
      
    },
    data() {
      return {
          tableData4: [],
      }
    },
    created: function () {
      var vm = this;

      vm.getresult();
    },

    methods: {
    getresult(){
       var vm = this;
        vm.tableData4 = [];
         var url = "http://localhost:8081/cleanresult";
        var data_tmp = {
        };
       $.ajax({
          url: url,
          type: "GET",
          dataType: 'json',
          data: {
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data.data;
            if(data.code==0){
            if(Obj.length>0){
              for (var i = 0; i < Obj.length; i++) {
                vm.tableData4.push({
                  "resultid":Obj[i].resultid,
                  "jobname":Obj[i].jobname,
                  "jobdesc":Obj[i].jobdesc,
                  "datatype":Obj[i].datatype,
                  "dataname":Obj[i].dataname,
                  "algtype":Obj[i].algtype,
                  "algname":Obj[i].algname,
                });
                
              }}}
              else{
                console.log(data);
              }
          },
           error: function (data) {
            console.log(data);
          }
        });
    },

    }

  }

</script>



